import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { WidgetTreeComponent } from './tree.component';
import { HttpClientModule } from '@angular/common/http';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { TranslateModule } from '@ngx-translate/core';
import { TreeModule } from 'angular-tree-component';
import { DndModule } from 'ng2-dnd';

import { TreeConfigModule } from 'app/shared/tree/tree-config.module';
import { FormControlService } from 'app/shared/input/form-control.service';
import { InputModule } from 'app/shared/input/input.module';
import { InformationModule } from 'app/shared/information/information.module';

@NgModule({
    imports: [
        CommonModule,
        HttpClientModule,
        NgbModule.forRoot(),
        TranslateModule,
        TreeModule,
        DndModule,
        TreeConfigModule,
        InputModule,
        InformationModule
    ],
    providers: [
        FormControlService,
    ],
    declarations: [
        WidgetTreeComponent
    ],
    entryComponents: [
    ],
    exports: [
        WidgetTreeComponent
    ]
})

export class WidgetComponentModule {}